@font-face {
  font-family: karla;
  src: url("Karla-Regular.3cd26653.ttf") format("truetype");
  font-style: normal;
  font-weight: 100;
}

@font-face {
  font-family: roboto;
  src: url("Roboto-Black.be41af2a.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #ebebeb;
  margin: 0;
  padding: 0;
  font-family: karla, serif;
  font-size: 22px;
  line-height: 1.3;
}

h1, h2, h3, h4, h5, h6 {
  font-variant: small-caps;
  margin: 0;
  font-family: roboto, serif;
  line-height: 1.1;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 28px;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  border-style: none;
}

:is(h1, h2, h3, h4, h5, h6) + :is(h1, h2, h3, h4, h5, h6) {
  margin-top: .2em;
}

:is(h1, h2, h3, h4, h5, h6) + :is(p, ul, ol) {
  margin-top: .7em;
}

p + :is(h1, h2, h3, h4, h5, h6) {
  margin-top: 1.3em;
}

p + p {
  margin-top: .5em;
}

.menu {
  color: #fff;
  background-color: #333;
  justify-content: flex-end;
  font-size: 18px;
  display: flex;
}

.menu li:hover {
  background-color: #464646;
}

.menu a {
  color: #fff;
  text-transform: uppercase;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.navigationBackground {
  background-color: #333;
}

.about_me_heading, .services_heading, .contact_heading {
  font-size: 40px;
}

.home_section_container {
  gap: 54px;
  margin-left: auto;
  margin-right: auto;
  padding: 36px 0;
  display: flex;
}

.about_services_background {
  background-color: #d1ccc9;
}

.portrait {
  width: 250px;
  border-radius: 9px;
  justify-content: flex-end;
  align-items: flex-end;
  margin-left: 24px;
}

.icon {
  flex-direction: column;
  justify-content: center;
  font-size: 180px;
  display: flex;
}

.fa-handshake {
  color: #151583;
}

.fa-address-card {
  color: #2daef8;
}

.fa-comments-dollar {
  color: #0d8109;
}

.services_heading {
  text-align: center;
  margin-bottom: 36px;
}

.services_box {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 52px;
  display: flex;
}

.services_picture {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.service_card_container {
  width: 360px;
  background-color: #cbcbccb0;
  border-radius: 6px;
  flex-direction: column;
  transition: all .2s;
  display: flex;
  overflow: hidden;
  box-shadow: 1px 1px 10px #000;
}

.service_card_container:hover {
  transform: scale(1.05);
}

.service_lists {
  letter-spacing: .1em;
  padding-left: 20px;
  font-size: 18px;
  font-weight: normal;
  list-style-type: circle;
}

.service_lists li + li {
  margin-top: .6em;
}

.service_category {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
}

.card_details {
  padding: 20px 30px;
}

.card {
  float: left;
  width: 100%;
  border-style: solid;
  border-width: 2px;
  margin: 25px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.1);
}

.banner {
  width: 100%;
  text-align: left;
  background-color: #385e9b;
  margin-top: 0;
  margin-bottom: 0;
  padding: 30px;
